          label: "收款单号",
          width: 200,
          slot: "",
        },
        {
          prop: "bill_price",
          label: "收款金额",
          width: 160,
          slot: "",
        },
      ],
    };
  },
  props: {
    type: {
      type: [Number, String],
      default: 0,
    },
    analyzeType: {
      type: [Number, String],
      default: 0,
    },
  },
  components: {
    // picking,
    lodopDelivery,
    popUp,
  },
  computed: {
    ...mapGetters(["threeoutstorageIndexGet", "printingStatusGet"]),
  },
  methods: {
    ...mapActions([
      "printingStatus",
      "threeoutstorageIndexACT",
      "threeoutstorageAddACT",
      "threeoutstorageModifyACT",
      "threeoutstorageSeeACT",
    ]),
    // 判断返回字段是否为0
    judgeValueIsShow(val, fileds) {
      const formatVal = parseFloat(val);
      let obj = {
        style: {
          color: "#2b85e4",
        },
        operate: {
          isClick: true,
        },
      };
      if (fileds) {
        if (fileds == "未发货") {
          obj.style.color = "#666";
          obj.operate.isClick = false;
        }
      } else {
        try {
          if (typeof formatVal == "number") {
            if (formatVal <= 0) {
              obj.style.color = "#666";
              obj.operate.isClick = false;
            }
          }
        } catch (error) {
          console.log(error);
        }
      }
      return obj;
    },
    // 清除对象字段
    clickClear(obj, keyArr) {
      keyArr.forEach((key) => {
        obj[key] = "";
      });
    },
    // popUP 分页切换
    currentChange_popUp(params) {
      this.popUpPage(params);
    },
    prevClick_popUp(params) {
      this.popUpPage(params);
    },
    nextClick_popUp(params) {
      this.popUpPage(params);
    },
    // popover hide
    popoverHide(scope, type) {
      scope.row[`popoverIsShowType_${type}`] = -1; // 修改标识符号
    },
    // 关闭列表弹窗卡片
    closeWinCard(scope) {
      const index = scope.$index;
      this.$refs["popBtn" + index].click();
    },
    // 开启卡片
    openWinCard(scope, labelType) {
      // labelType 1 回款金额 2 回退金额 3 发货状态
      this.delayedLoad = true;
      const index = scope.$index;
      // 初始化数据
      // this.$set(this.TableData.tableData[index], "detailList", []);
      // this.$set(this.popUpTable, "detailList", []);
      this.popUpTable.isShowPage = false;
      this.popUpTable.detailList = [];
      let popUpRefs = "",
        searchFields = ""; // 要使用的字段
      switch (labelType) {
        case 1:
          searchFields = "receipt_price";
          break;
        case 2:
          searchFields = "refund_price";
          break;
        case 3:
          searchFields = "delivery_status";
          break;
      }
      let isClick = this.judgeValueIsShow(scope.row[searchFields]).operate
        .isClick;
      if (!isClick || scope.row[`popoverIsShowType_${labelType}`] == 1) return;
      // this.$set(this.TableData.tableData[index], "tableLoading", true);
      // this.popUpTable.tableLoading = true; // 使用上面方法后初次加载该字段可以不设置
      // 加载table缓解浏览器的渲染
      this.isShowTable({ index, type: labelType }, false);
      this.$set(
        this.TableData.tableData[index],
        `popoverIsShowType_${labelType}`,
        1
      ); // 标识弹框已经打开
      setTimeout(() => {
        // 必须使用下延时器 否则出现得弹框会有闪烁问题
        this.port_receipt_price_list({
          outstock_sn: scope.row.outstock_sn,
          index: index,
          type: labelType,
        });
      }, 500);
    },
    // popUp 翻页
    popUpPage(params) {
      // type/labelType 1 回款金额 2 回退金额 3 发货状态
      let { type, page = 1, index } = params;
      let row = this.TableData.tableData[index],
        outstock_sn = row.outstock_sn;
      params.outstock_sn = outstock_sn;
      // this.$set(this.TableData.tableData[index], "tableLoading", true); // 加载
      this.popUpTable.tableLoading = true;
      this.port_receipt_price_list(params);
    },
    // popover 加载层及加载table缓解浏览器的渲染
    isShowTable(params, bool) {
      let { type, index } = params;
      // 显示table层(缓解浏览器的渲染)
      let tabelShowType_Name = `tabelShowType_${type}`;
      this.$set(this.TableData.tableData[index], tabelShowType_Name, bool);
    },
    // 回款/回退/状态 明细接口返回处理
    dataDeal(res, params) {
      let { outstock_sn, page = 1, index, type } = params;
      // 加载table缓解浏览器的渲染
      this.isShowTable(params, true);
      if (res.data.code == 200) {
        let getData = res.data.data;
        // this.$set(this.TableData.tableData[index], "detailList", data.data);
        if (type == 1) {
          this.popUpTable.detailList = getData;
        } else {
          this.popUpTable.detailList = getData.data;
        }
        // this.$set(this.TableData.tableData[index], "pageTotal", data.total);
        this.popUpTable.pageTotal = getData.total;
        if (getData.data && getData.data.length > 0) {
          // this.$set(this.TableData.tableData[index], "isShowPage", true);
          this.popUpTable.isShowPage = true;
        }
      }
      this.popUpTable.tableLoading = false;
    },
    //接口 type/labelType = 1/2/3 明细
    port_receipt_price_list(params) {
      // type/labelType 1 回款金额 2 回退金额 3 发货状态
      let { outstock_sn, page = 1, index, type } = params;
      // this.$set(this.TableData.tableData[index], "current_page", page); // 重置显示页数
      this.popUpTable.current_page = page;
      let portName = "";
      switch (type) {
        case 1:
          portName = "inventorAuditorReceiptPriceList";
          break;
        case 2:
          portName = "inventorAuditorRefundPrice";
          break;
        case 3:
          portName = "inventorAuditorDeliveryDetail";
          break;
      }
      if (!portName) return;
      this.$axMethod[portName]({
        outstock_sn: outstock_sn,
        page: page,
      })
        .then((res) => {
          this.dataDeal(res, params);
        })
        .catch((error) => {
          // 加载table缓解浏览器的渲染
          this.isShowTable(params, true);
          // this.$set(this.TableData.tableData[index], "tableLoading", false); // 清除加载
          this.popUpTable.tableLoading = false;
        });
    },
    exportOrder(scheduleType) {
      // 导出库存收发明细表导出URL
      let instaidarry = this.multipleSelection.map((item) => {
        return item.outstock_id;
      });
      let s = Object.assign(this.params, {
        export_type: scheduleType,
        id_arr: instaidarry.toString(),
      });
      this.$axMethod.exportOtherThreeoutstorage(this.params).then((res) => {
        if (res.data.code == 200) {
          window.location.href = res.data.data.url;
        }
      });
    },
    handleCommand(common, scope) {
      if (["seeF", "modifyF"].includes(common)) {
        this[common](scope);
      } else {
        this.lodopDe(scope, 0, common);
      }
    },
    printChange(val) {
      let companyFlag = false;
      const companyIds = this.multipleSelection;
      for (var i = 1; i < companyIds.length; i++) {
        if (companyIds[i - 1].company_id != companyIds[i].company_id) {
          companyFlag = true;
          break;
        }
      }
      if (!this.multipleSelection.length) {
        this.$message({
          message: "请选择单据进行打印",
          type: "error",
        });
      } else if (companyFlag) {
        this.$message({
          message: "请选择相同公司单据进行打印",
          type: "warning",
        });
      } else {
        let flag = true,
          errorGoodsArr = [];
        this.multipleSelection.forEach((item) => {
          if (item.after_auditor_print_status == 1) {
            //审核后才可打印
            if (item.auditor_status_name != "已审核") {
              errorGoodsArr.push(item.outstock_sn);
              flag = false;
            }
          }
        });
        if (flag) {
          if (val == 1) {
            this.multipleSelection.forEach((val) => {
              this.lodopDe(val, true, "warrantyPickingOrders");
            });
            //拣货单
          } else {
            this.multipleSelection.map((item) => item.auditor_status);
            let flag = true;
            if (flag) {
              if (val == 1) {
                this.multipleSelection.forEach((val) => {
                  this.lodopDe(val, true, "warrantyPickingOrders");
                });
                //拣货单
              } else {
                //出库单
                this.multipleSelection.forEach((item) => {
                  this.lodopDe(item, true, "warrantyOutbound");
                });
              }
            } else {
              this.$message.warning("仅能打印已审核的单据");
            }
          }
        } else {
          let str = errorGoodsArr.map((item, i) => `${i + 1}.${item}`);
          this.$alert(
            `以下未审核的单据不可打印：<br/>
            <div style="color:#e6a23c">
              ${str.join("，")}
            <div>`,
            "提示",
            {
              dangerouslyUseHTMLString: true,
            }
          );
        }
      }
    },
    getSummaries(param) {
      const { columns, data } = param;
      // let propPageArr = ["my_pro"], //本页需要合计计算的字段
      //   sumPropArr = ["my_pro"]; // 总计需要合计计算的字段
      // let fileds = "customer_name"; // 在客户名称处进行添加合计或总计字段
      const sums = []; // 最后返回的
      //   pageSum = [], // 存储页总计
      //   totalSum = []; // 存储合计

      // columns.forEach((column, index) => {
      //   const isIncludes = propPageArr.includes(column["property"]); // 判断是否包含
      //   if (column["property"] == fileds) {
      //     pageSum[index] = "本页合计"
      //     return
      //   }
      //   if (isIncludes) {
      //     // 如果为True
      //     console.log()
      //   } else {
      //     pageSum[index] = "";
      //   }
      // });
      columns.forEach((column, index) => {
        if (index === 5) {
          sums[index] = "本页合计";
          return;
        }

        const values = data.map((item) => Number(item[column.property]));
        if (!values.every((value) => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index === 13) {
            sums[index] = parseFloat(sums[index])
              .toFixed(2)
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
            return;
          } else if (index === 14) {
            sums[index] = parseFloat(sums[index])
              .toFixed(2)
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
            return;
          } else if (index === 15) {
            sums[index] = parseFloat(sums[index])
              .toFixed(2)
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
            return;
          } else if (index === 16) {
            sums[index] = parseFloat(sums[index])
              .toFixed(2)
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
            return;
          } else {
            sums[index] = "";
            return;
          }
        } else {
          sums[index] = "";
        }
      });
      return sums;
    },
    lodopDe(row, index = 0, name) {
      this.printingStatus(true);
      this.$set(this, "title", name);
      this.$nextTick(() => {
        this.$refs.lodopDelivery.printPdf(
          {
            outstock_id: row.outstock_id,
          },
          index
        );
      });
    },
    companyChange(val) {
      if (val) {
        this.params.company_name = this.companyList.filter(
          (obj) => obj.company_id === val
        )[0].company_name;
      }
      this.$set(this.params, "department_id", "");
      this.$set(this.params, "customer_id", "");
      this.$set(this.params, "logistics_id", ""); // 初始化物流公司选中项
    },
    // 物流公司选中项改变
    getlogisticsChange(val) {
      if (val) {
        this.params.settlement_customer_name = this.logisticsList.filter(
          (obj) => obj.logistics_id === val
        )[0].logistics_name;
      }
    },
    shenHe() {
      //审核
      let vm = this;
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要审核的信息！",
          type: "warning",
        });
        return false;
      }
      let addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].outstock_id);
      }
      let creditArr = addr.toString();
      let funcan = {
        eachData: {
          outstock_id: creditArr.toString(),
        },
        UrlApi: "auidtorPassThreeOutStorage",
        list: {
          UrlApi: "v2GetThreeOutStorageList",
          data: this.TableData,
          esData: this.params,
        },
      };
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 13,
          bill_id: creditArr,
          operation_type: 4,
        })
        .then(() => {
          // 校验安全库存金额
          this.$axMethod
            .safetyStockCheck({
              bill_logogram: "tbck",
              order_type: 2,
              bill_id: creditArr,
            })
            .then(() => {
              repertory(
                this,
                {
                  goods_info: creditArr,
                  status: 2,
                  type: 3,
                },
                function () {
                  vm.$common.audit(funcan);
                }
              );
            });
        });
    },
    // 作废
    abolishFun() {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要作废的信息！",
          type: "warning",
        });
        return false;
      }
      let addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].outstock_id);
      }
      let creditArr = addr.toString();
      let funcan = {
        eachData: {
          outstock_id: creditArr.toString(),
        },
        UrlApi: "ThreeauditorInvalid",
        list: {
          UrlApi: "v2GetThreeOutStorageList",
          data: this.TableData,
          esData: this.params,
        },
      };
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 13,
          bill_id: creditArr,
          operation_type: 6,
        })
        .then(() => {
          this.$common.audit(funcan);
        });
    },
    AUDIT_GET() {
      //弃审
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要弃审的信息",
          type: "warning",
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].outstock_id);
      }
      var rebateArr = addr.toString();
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 13,
          bill_id: rebateArr,
          operation_type: 5,
        })
        .then(() => {
          this.$axMethod
            .inventorAuditorNoPass({
              outstock_id: rebateArr,
            })
            .then((res) => {
              if (res.data.code == 200) {
                this.$message({
                  message: res.data.msg,
                  type: "success",
                });
                getData(this.AccordingParams, 2, this.tableDataListCall); //获取销售订单列表
              }
            });
          //弃审
        });
    },
    departmentChange(val) {
      if (val) {
        this.params.department_name = this.departmentList.filter(
          (obj) => obj.department_id === val
        )[0].department_name;
      }
      this.$set(this.params, "buyer_id", "");
    },
    getCustomer(val) {
      if (val) {
        this.params.customer_name = this.customerList.filter(
          (obj) => obj.customer_id === val
        )[0].customer_name;
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.timeValue = [];
      this.timeValue1 = [];
      this.$set(this, "params", {
        auditor_status: 0, // wz 审核状态
        // refund_price_status: 0, // wz 回款状态
        deliver_status: 0, // wz 发货状态
        payment_method: 0, // wz 回款方式
      });
      this.threeoutstorageIndexACT({
        goPage: this.goPage,
        params: this.params,
        pageSize: this.pageSize,
      });
      this.$set(this, "AccordingParams", {
        params: this.params,
        TableData: this.TableData, //列表数据
        API: "v2GetThreeOutStorageList", //接口名
        vm: this, //传递的this
      });
      getData(this.AccordingParams, 2, this.tableDataListCall);
    },
    seeF(row) {
      this.threeoutstorageSeeACT({
        outstock_id: row.outstock_id,
        goPage: this.goPage,
        params: this.params,
        pageSize: this.pageSize,
      });
      this.$router.push({
        path: "/inventory/threeoutstorage/threeoutstorageSee",
        query: { id: row.outstock_id },
      });
    },
    addF(scope, row) {
      this.$router.push({
        path: "/inventory/threeoutstorage/threeoutstorageAdd",
      });
    },
    modifyF(row) {
      if (row && row.is_execute == 1) {
        this.$message.error("wms系统正在操作此单据，暂不支持单据修改");
        return false;
      }
      this.threeoutstorageModifyACT({
        outstock_id: row.outstock_id,
        order_type: row.order_type,
        goPage: this.goPage,
        params: this.params,
        pageSize: this.pageSize,
      });
      this.$router.push({
        path: "/inventory/threeoutstorage/threeoutstorageModify",
      });
    },
    onSearch() {
      this.b = false;
      if (this.timeValue) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      if (this.timeValue1) {
        this.params.auditor_start_time = this.timeValue1[0];
        this.params.auditor_end_time = this.timeValue1[1];
      } else {
        this.params.auditor_start_time = this.params.auditor_end_time = "";
      }
      Object.assign(this.AccordingParams.params, this.params, {
        page: 1,
      });
      this.threeoutstorageIndexACT({
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize,
      });
      getData(this.AccordingParams, 2, this.tableDataListCall);
    },
    //删除
    deletes() {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要删除的信息",
          type: "warning",
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].outstock_id);
      }
      var rebateArr = addr.toString();
      var deledata = {
        deleID: "outstock_id",
        deleSelect: this.multipleSelection,
        deleApi: "delThreeOutStorage",
        list: {
          UrlApi: "v2GetThreeOutStorageList",
          data: this.TableData,
          esData: this.params,
        },
      };
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 13,
          bill_id: rebateArr,
          operation_type: 3,
        })
        .then(() => {
          this.$common.deletes(deledata, "删除", this);
        });
    },
    handleSizeChange(val) {
      Object.assign(this.AccordingParams.params, {
        per_page: val,
        page: 1,
      });
      getData(this.AccordingParams, 2, this.tableDataListCall);
    },
    handleCurrentChange(val) {
      Object.assign(this.AccordingParams.params, {
        page: val,
      });
      getData(this.AccordingParams, 2, this.tableDataListCall);
    },
    // 接口回调
    tableDataListCall() {
      this.TableData.tableData.forEach((item) => {
        this.$nextTick(() => {
          this.$set(item, "popoverIshow", 1);
        });
      });
    },
  },

  mounted() {
    this.hasAuditorStatusPrint = window.localStorage.hasAuditorStatusPrint;
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.$set(this, "params", {
      auditor_status: 0, // wz 审核状态
      // refund_price_status: 0, // wz 回款状态
      deliver_status: 0, // wz 发货状态
      payment_method: 0, // wz 回款方式
    });
    // 监控table 滚动
    const elTable1 = this.$refs.table1.$el.getElementsByClassName(
      "el-table__body-wrapper"
    )[0];
    const elTable2 = this.$refs.table_sum;
    elTable1.addEventListener("scroll", function () {
      elTable2.scrollLeft = this.scrollLeft;
    });
    // 监控滚动
    if (this.threeoutstorageIndexGet && this.threeoutstorageIndexGet.params) {
      if (this.threeoutstorageIndexGet.params.start_time) {
        const { start_time, end_time } = this.threeoutstorageIndexGet.params;
        this.timeValue = this.timeValue.concat([start_time, end_time]);
      }
      if (this.threeoutstorageIndexGet.params.auditor_start_time) {
        const { auditor_start_time, auditor_end_time } =
          this.threeoutstorageIndexGet.params;
        this.timeValue1 = this.timeValue1.concat([
          auditor_start_time,
          auditor_end_time,
        ]);
      }
      opthionList(this, this.threeoutstorageIndexGet.params);
      this.$set(this, "params", this.threeoutstorageIndexGet.params);
      this.logisticsList.push({
        logistics_id: this.params.logistics_id,
        logistics_name: this.params.settlement_customer_name,
      });
    }
    if (this.threeoutstorageIndexGet.goPage) {
      this.params.page = this.threeoutstorageIndexGet.goPage;
      this.params.per_page = this.threeoutstorageIndexGet.pageSize;
    }
    this.$set(this, "AccordingParams", {
      params: this.params, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "v2GetThreeOutStorageList", //接口名
      vm: this, //传递的this
    });
    getData(this.AccordingParams, 2, this.tableDataListCall);
  },
};
